.experience-container {
    margin: 4rem 0;
    position: relative;
    margin: auto;
    margin-top: 4rem;
    max-width: 95%;
}

.experience-container h5 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 2.5rem;
}

.arrow-left,
.arrow-right {
    width: 2.2rem;
    height: 2.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #6751b9;
    border: 1.5px solid #6751b9;
    /* color: #ff014f; */
    /* border: 1.5px solid #ff014f; */
    border-radius: 0.65rem;
    /* background: #130f2a; */
    position: absolute;
    top: 55%;
    z-index: 2;
    cursor: pointer;
}

.arrow-left span,
.arrow-right span {
    font-size: 2rem;
    font-weight: 500;
}

.arrow-left {
    left: -2rem;
}

.arrow-right {
    right: -2rem;
}

.slick-initialized .slick-slide{
    padding: 15px;
}

/* .setwh {
    width: 5rem;
    height: 3rem;
} */

@media (max-width: 1025px) {

    .arrow-left {
        left: 0rem;
    }

    .arrow-right {
        right: 0rem;
    }

}

@media (max-width: 768px){
    .experience-container h5 {
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }

    .arrow-left {
        left: -1rem;
    }

    .arrow-right {
        right: -1rem;
    }
}