.skills-info-card {
    min-height: 23rem;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    /* background: rgba(22,17,47,0.398); */
    background: #ecf0f3;
    backdrop-filter: blur(1rem);
}

.skills-info-card h6{
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0.8rem 2rem;
    /* background: linear-gradient(180deg, #ac96ff 0%, #7c5fe6 100%); */
    /* background: linear-gradient(145deg, #e2e8ec, #ffffff); */
    background: linear-gradient(145deg, #ff014f,#ff014f);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-bottom: 1.5px solid #6751b9;
}

.skills-info-content {
    padding: 2rem;
}

.skills-info {
    /* display: flex; */
    display: block;
    align-items: center;
    justify-content: space-between;
}

.skills-info p {

    font-size: 1rem;
    font-weight: 500;
}

.skills-info .percentage {
    /* color: #dd8cfa; */
    color: #ff014f
}

.skill-progress-bg {
    width: 100%;
    height: 0.5rem;
    background: #382e68;
    /* background-color: #ecf0f3; */
    border-radius: 0.5rem;
    margin: 1rem 0;
    overflow: hidden;
}

.skill-progress {

    width: 0%;
    height: 0.5rem;
    /* background: linear-gradient(90deg, #aa94fe -50%, #7d60e6 50%); */
    background: linear-gradient(145deg, #ff014f,#ff014f);
    color:  #ff014f;
    border-radius: 0.5rem;
    transition: all 0.5s ease-in-out;
}


@media (max-width: 1025px) {
    .skills-info-card h6 {

        font-size: 1.1rem;    
    }

    .skills-info p {

        font-size: 0.9rem;
    }

    .skills-info-content {
        padding: 1.5rem;
    }
}

